/* user style */

*,
*::before,
*::after {
    box-sizing: border-box;
}

html,
body,
p,
ul,
figure,
button,
h1,
h2,
h3,
h4,
aside,
select,
textarea,
input {
    border: none;
    margin: 0;
    padding: 0;
    list-style: none;
    resize: none;
    font-weight: normal;
    background-color: unset;
}

select,
textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
}

select:focus,
textarea:focus,
button:focus,
input:focus {
    outline: none;
}

a {
    color: inherit;
    text-decoration: none;
}

img {
    display: block;
}

::-webkit-scrollbar {
    width: 0;
}
